.page-body-wrapper {
  min-height: calc(100vh - #{$navbar-height});
  @include display-flex();
  @include flex-direction(row);
  padding-left: 0;
  padding-right: 0;
  padding-top: $navbar-height;

  &.full-page-wrapper {
    width: 100%;
    min-height: 100vh;
    padding-top: 0;
  }
}

.main-panel {
  transition: width $action-transition-duration $action-transition-timing-function, margin $action-transition-duration $action-transition-timing-function;
  width: calc(100% - #{$sidebar-width-lg});
  min-height: calc(100vh - #{$navbar-height});
  @include display-flex();
  @include flex-direction(column);
  @media (max-width: 991px) {
    margin-left: 0;
    width: 100%;
  }
}

.content-wrapper {
  background: $content-bg;
  padding: 2.375rem 2.375rem;
  width: 100%;
  @include flex-grow(1);
  @media(max-width: 767px) {
    padding: 1.5rem 1.5rem;
  }
}

.brand-name{
  color: #248afd;
  font-weight: bolder;
  margin-bottom: 0.3px;
  font-size: larger;
}

.brand-small{
  color: #248afd;
  font-weight: bolder;
  margin-bottom: -4.5px;
  font-size: larger;
}

.brand-page-name{
  color: #000;
  font-weight: bold;
  margin-left: 10px;
  margin-top: 10px;
}

@media (max-width: 576px) {
  .brand-page-name {
    display: none !important;
  }
}

.navbar-nav .nav-item .icon-mail{
  font-size: 1.25rem;
}

.nav-user-info{
  text-align: right !important;
  line-height: 0.8rem;
  margin-top: 7px;
}

.breadcrumb{
  border: unset;
}

.create-project-btn{
  margin: 20px;
}

.table-border{
  border: 1px solid #f7f7f7 !important;
}

.input-group{
  background: #fff;
}

.border-radius{
  border-radius: 15px !important;
}

.divider {
    display: block;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    margin: 1rem 0;
}

.divider .divider-text {
    position: relative;
    display: inline-block;
    font-size: 0.9375rem;
    padding: 0 1rem;
}

.divider .divider-text:before {
    right: 100%;
}

.divider .divider-text:before, .divider .divider-text:after {
    content: '';
    position: absolute;
    top: 50%;
    width: 9999px;
    border-top: 1px solid #ebe9f1;
}

.divider .divider-text:after {
    left: 100%;
}
.divider .divider-text:before, .divider .divider-text:after {
    content: '';
    position: absolute;
    top: 50%;
    width: 9999px;
    border-top: 1px solid #ebe9f1;
}